<template>
    <section id="fourth_page_cont" class="page_cont">
        <div id="events_metacont">

                <span id="calendar_cont">
                    <b-calendar id="calendar" v-model="date" :date-disabled-fn="dateClass" locale="it" block>
                        <div class="d-flex calendar_btns_cont" dir="ltr">
                        <b-button
                            size="sm"
                            variant="outline-primary"
                            class="ml-auto"
                            @click="setToday"
                        >
                            Imposta oggi
                        </b-button>
                        <b-button
                            size="sm"
                            variant="outline-danger"
                            class="ml-auto"
                            v-if="date"
                            @click="clearDate"
                        >
                            Deseleziona
                        </b-button>
                        </div>
                    </b-calendar>
                </span>

                <span id="events_cont">
                    <div>
                        <b-card-group deck>
                            <div v-for="(event,i) in this.displayable_events" :key="i+event" style="margin-bottom:3%;">

                   
                            <b-card title="Title" img-src="https://picsum.photos/300/300/?image=41" img-alt="Image" img-top style="height: 250px;"
                            >
                         
                                
                                <b-card-text>
                                This is a wider card with supporting text below as a natural lead-in to additional content.
                                This card has even longer content than the first to show that equal height action.
                                </b-card-text>
                                
                                <div class="card_social_cont_event">
                                    <div class="for_social" v-for="(social_key,j) in Object.keys(event).filter(f=>f.includes('social_'))" :key="i.toString()+social_key+j.toString()">
                                        <span v-if="event[social_key]!==''"> <a :href="event[social_key]" target="_blank"><font-awesome-icon :icon="`fa-brands fa-${social_key.replace('social_','')}`"/> </a> </span> 
                                    </div>
                                </div>

                             
            

                            <div class="card-footer">
                                    <small class="text-muted">{{event.date}}</small>
                                </div>
                            </b-card>
                            
                                     </div>
                        </b-card-group>
                        </div>
                </span>

        </div>
    </section>
</template>

<script>
export default{
    name:"FourthPage",
    props:{},
    data(){
        return{
            date:'',
            events:[],
            displayable_events:[]
        }
    },
    mounted(){
        this.events= this.$parent.firebase_data["slides"]["events"]
        this.displayable_events=this.events


    },
    methods:{
        setToday() {
        const now = new Date()
        this.date = new Date(now.getFullYear(), now.getMonth(), now.getDate())
      },
      clearDate() {
        this.date = ''
      },
        dateClass(ymd, date) {
        //date-info-fn works in bootstrapvue 4.0.0 and not in 4.4.0. Chaging it resolves this but makes other things worst.
        //so disable all dates that are not in data
        //selecting them by year, month and date!
        //pass this function to :date-disabled-fn
        console.log(date.getDate().toString())

            //CONTINUA DA QUI!
            let day = date.getDate().toString().length>1 ?date.getDate().toString() :"0"+date.getDate().toString()
            let yr=date.getFullYear()
            let month=(date.getMonth()+1).toString().length>1 ?(date.getMonth()+1).toString() :"0"+(date.getMonth()+1).toString()

            const event_dates= this.events.map(d=>d["date"])
            console.log(event_dates, `${yr}-${month}-${day}`)
            if (event_dates.includes(`${yr}-${month}-${day}`)){
                return false
            }else{
                return true
            }

      }
    },
    watch:{
        date: function (){
            console.log("DATE", this.date)
            if (this.date!==""){
                this.displayable_events = this.events.filter(f=>f["date"]==this.date)
            }else{
                 this.displayable_events = this.events
            }
        }
    }
}
</script>

<style>
    .b-calendar-grid-body{
        background-color: #0dcaf06e !important;
    }

    #events_cont{
        width: 75%;
        text-align: center;
        padding: 3%;

    }

    #event_cont > .card{
        text-align: center;
        height: 100%;
        font-size: 80%;
    }

    .card-img-top{
        max-width: 225px;
        margin-top:12px;
        margin-left:24px;
        position: relative;
        border-radius: 100% !important;
    }

    .card-body{
        position: absolute;
        text-align: center;
        margin-left: 25%;
        height: 100%;
    }

    .event_url_cont{
        position: absolute;
        bottom: 30px;
        text-align: center;
        width: 100%;
    }
    .card-footer{
        position: absolute;
        bottom: 0;
        width: 100%;
        left:0;

    }

    .card-text{
        width: 50%;
    margin-left: 25%;
    }

    .card_social_cont_event{
        position: absolute;
        right:20px;
        top:15px;
        text-align: justify;
        justify-content: space-evenly;

        
    }
    .calendar_btns_cont{
        display: flex;
        flex-direction: row;
        width:100%;
    }

    .calendar_btns_cont >button{
        width: 100%!important;
        flex:1
    }
    
    
    @media only screen and (min-width: 990px) {     
    #events_metacont{
            display: flex;
            flex-direction: row;
            width: 100%;
        }
    
    .b-calendar{
        position: fixed;

    }
    
    #events_cont{
        margin-left: 280px;
    }

}

@media only screen and (max-width: 990px) {
     #events_metacont{
            display: flex;
            flex-direction: column;
            width: 100%;
        }


    #events_cont > .card{
        height: 250px ;
    }

    #events_cont{
        width: 100%;
        padding: 3%;
    }


    .card-body{
        padding: 3%;
    }

    .card-text{
        width: 100%;
        margin-top:10px;
        margin-left: -15%;
        max-height: 100px;
        overflow: scroll;
        
    }

    .card-img-top{
        max-width: 65px;
        margin-top:12px;
        margin-left:24px;
        position: absolute;
        border-radius: 100% !important;
        
    }

    .card-title{
        margin-left: -20%;
    }

    .event_url_cont{
        margin-left: -20%
    }

    .card-footer{
        margin-left: -20%;
    }
    

}


    

</style>
